/* Page container */
.canvas-login-page {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  box-sizing: border-box;
}

/* Login card */
.canvas-login-box {
  width: 100%;
  max-width: 420px;
  padding: 2rem;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
  backdrop-filter: saturate(120%) blur(2px);
}

.canvas-logo {
  margin: 0 0 1rem 0;
  font-size: 1.8rem;
  font-weight: 700;
  letter-spacing: 1px;
  color: #2d3b45;
  text-align: center;
}

.error-message {
  margin: 0 0 1rem 0;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  background: #fff5f5;
  color: #b00020;
  border: 1px solid #ffd6d6;
  font-size: 0.95rem;
}

form label {
  display: block;
  margin: 0.5rem 0 0.25rem;
  color: #2d3b45;
  font-weight: 600;
  font-size: 0.95rem;
}

form input[type="email"],
form input[type="password"] {
  width: 100%;
  padding: 0.75rem 0.9rem;
  border: 1px solid #d1d9e0;
  border-radius: 8px;
  outline: none;
  font-size: 1rem;
  box-sizing: border-box;
  transition: border-color 0.2s, box-shadow 0.2s;
}

form input[type="email"]:focus,
form input[type="password"]:focus {
  border-color: #2684ff;
  box-shadow: 0 0 0 3px rgba(38, 132, 255, 0.15);
}

.options {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0.75rem 0 1rem;
}

.checkbox {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: #2d3b45;
}

.forgot-password {
  color: #2684ff;
  text-decoration: none;
  font-weight: 600;
}

.forgot-password:hover {
  text-decoration: underline;
}

.canvas-login-btn {
  width: 100%;
  padding: 0.85rem 1rem;
  border: none;
  border-radius: 8px;
  background: #2684ff;
  color: #fff;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s, transform 0.02s;
}

.canvas-login-btn:hover {
  background: #1e6fdb;
}

.canvas-login-btn:active {
  transform: translateY(1px);
}

.footer-logo {
  margin-top: 1rem;
  text-align: center;
  color: #6b7c8a;
  font-size: 0.85rem;
  letter-spacing: 0.05em;
}

/* Small screens */
@media (max-width: 480px) {
  .canvas-login-box {
    padding: 1.25rem;
    border-radius: 10px;
  }

  .canvas-logo {
    font-size: 1.5rem;
  }
}
