.change-password-page {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.change-password-box {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 2.5rem;
  border-radius: 10px;
  width: 380px;
  text-align: left;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

  --field-gap: 12px;
}

.change-password-title {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1.2rem;
}

form label {
  display: block;
  margin: var(--field-gap) 0 0.35rem;
}
form label:first-of-type {
  margin-top: 0;
}

input[type="password"] {
  width: 100%;
  padding: 10px;
  border: 1px solid transparent;
  border-radius: 3px;
  font-size: 1rem;
  margin-bottom: 10px;
  height: 40px;
  box-sizing: border-box;
}

input:focus {
  outline: none;
  border: 1px solid #ccc;
  background-color: #fff;
  color: #000;
}

.change-password-btn {
  width: 100%;
  padding: 10px;
  background-color: transparent;
  border: 1px solid white;
  color: white;
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.change-password-btn:hover {
  background-color: white;
  color: #2d3b45;
}

.message {
  margin-bottom: 1rem;
  text-align: center;
  font-size: 0.9rem;
  padding: 8px;
  border-radius: 5px;
}

.message.success {
  background-color: #d4edda;
  color: #155724;
}

.message.error {
  background-color: #f8d7da;
  color: #721c24;
}
